.card {
  box-shadow: 7px 7px 5px 1.25px rgba(0, 0, 0, 0.25);
  border-radius: 0.25rem;
  margin: 0 auto;
  margin-bottom: 40px;

  @include mediaquery(map-get($queries, md)) {
    max-width: 350px !important;
    min-width: 350px !important;
  }

  @include mediaquery(map-get($queries, lg)) {
    max-width: 400px !important;
    min-width: 400px !important;
  }

  @include mediaquery(map-get($queries, xl)) {
    max-width: 450px !important;
    min-width: 450px !important;
  }

  &.team-card {
    margin: 0 0 40px 0;

    max-width: 350px !important;
    min-width: 350px !important;
    transition: all ease 0.3s;
    &:hover {
      background: map-get($colors, _yellow);
    }
  }

  .card-body {
    padding-top: 0 !important;

    .card-title {
      text-align: left;
      color: map-get($colors, _blue);
    }
    .card-text {
      text-align: left;
      color: map-get($colors, _blue);
      font-weight: 600;
      margin-bottom: 0.5rem !important;
      font-size: 0.9em;
      transition: all ease 0.3s;
      svg {
        margin-right: 15px;
      }
      &.sizes {
        margin-bottom: 0.15rem !important;
      }

      &.height {
        margin-left: 65px;
      }

      &.price {
        color: map-get($colors, _red);
        font-weight: bold;
        font-size: 2em;
      }
      &.data {
        &:hover {
          text-decoration: none;
          // transform:scale(1.1,1.1);
          color: map-get($colors, _red);
          cursor: pointer;
        }
      }
    }

    .card-info-container {
      justify-content: space-between;
      padding: 0 15px;
      &.team-card {
        display: flex;
        flex-direction: column;

        padding: 0;
      }
    }
  }
}
